import styled from 'styled-components'
import { inject, observer } from 'mobx-react'

function Trades({lang,trading}){
  const colums = [
    {label : lang['size'],key : 'size'},
    {label : lang['price'],key : 'price'},
    {label : lang['time'],key : 'time'},
  ]
  const Wrapper = styled.div`
    font-size : 12px;
    margin : 8px 24px 0 24px;
    .t-title {
      color : #E0ECFF;
      font-size : 14px;
      height: 40px;
    }
    .t-header {
      display : flex;
      justify-content: space-between;
    }
    .t-body {
      color : #E0ECFF;
      .t-row {
        height: 24px;
      }
    }
    .
  `
  return (
    <Wrapper>
      <div className='t-title'>{lang['trades']}</div>
        <div className='t-header'>
          {colums.map((row,index) => (
            <div key={index}>{row.label}</div>
          ))}
        </div>
        <div className='t-body'>

        </div>
    </Wrapper>
  )
}
export default inject('trading')(observer(Trades))