import styled from 'styled-components'
import React, { useState } from 'react';

export default function Tab({ tabSet, inactiveBgColor,activeBgColor,icon, className, width = 238, height = 47, size = 16,borderTop = false,borderBottom = false, divider = false }) {
  const arr = Object.keys(tabSet)
  const [selected, setSelected] = useState(arr[0])
  const styles = tabSet[selected].style
  let borderT = borderTop ? `border-top:1px solid ${tabSet[selected].borderColor}`:"border-top:0"
  let borderB = borderBottom ? `border-bottom:1px solid ${tabSet[selected].borderColor}`:"border-bottom:0"
  let borderL = divider ? `border-left:1px solid ${tabSet[selected].borderColor}`:"border-left:0"
  let borderR = divider ? `border-right:1px solid ${tabSet[selected].borderColor}`:"border-right:0"
  const Wrapper = styled.div`
    cursor: pointer;
    width:${width}px;
    height:${height}px;
    display:flex;
    .tab-left.inactive{
      border-radius:0px 0px 4px 0px;
    }
    .tab-right.inactive{
      border-radius:0px 0px 0px 4px;
    }
    .tab-left.active{
      border-radius:0px 4px 0px 0px;
      ${borderR};
      height:14px;
    }
    .tab-right.active{
      border-radius:4px 0px 0px 0px;
      ${borderL};
      height:14px;
    }
    .inactive,.active{
      width:50%;
      box-sizing: content-box;
      padding: 15px 24px;
      font-size:${size}px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .inactive{
      background:${inactiveBgColor};
      ${borderB}
    }
    .active {
      color: ${tabSet[selected].fontColor ? tabSet[selected].fontColor : '#FFFFFF'};
      background : ${activeBgColor};
      ${borderT};
    }
  `
  function select(item) {
    setSelected(item)
  }

  return (
    <div style={styles} className={className}>
      <Wrapper className='width'>
        {arr.map(item => (
          <div
            key={item}
            className={item === selected ? `${tabSet[item].direction} active` : `${tabSet[item].direction} inactive`}
            onClick={() => select(item)}>
            {item}
          </div>
        ))}
      </Wrapper>
      {tabSet[selected].content}
    </div>
  )
}