import { inject ,observer} from "mobx-react"
import styled from 'styled-components'

function History({lang,trading}){
  const colums = [
    {label : lang['time'],key : 'time'},
    {label : lang['direction'],key : 'direction'},
    {label : lang['symbol'],key : 'symbol'},
    {label : lang['price'],key : 'price'},
    {label : lang['volume'],key : 'volume'},
    {label : lang['notional'],key : 'marketPrice'},
    {label : lang['transaction-fee'],key : 'transactionFee'}
  ]
  const Wrapper = styled.div`
    margin-top : 16px;
    overflow: auto;
    white-space: nowrap;
    
    .h-header {
      display : flex;
      justify-content: space-between;
      height: 40px;
      align-items : center;
    }
    .h-body {
      height : 128px;
      overflow : auto;
    }
    .h-col {
      margin-right : 16px;
    }
  `
  return (
    <Wrapper>
      <div className='h-header'>
        {colums.map((col,index) => (
          <div key={index} className='h-col'>{col.label}</div>
        ))}
      </div>
      <div className='h-body'>
      </div>
    </Wrapper>
  )
}
export default inject('trading')(observer(History))