import styled from 'styled-components'
import UnderlineText from "../../../components/UnderlineText/UnderlineText";
import DeriNumberFormat from "../../../utils/DeriNumberFormat";

export default function TradeInfo({lang}){
  const Wrapper = styled.div`
    display : flex;
    height: 96px;
    align-items: center;
    justify-content: space-between;
    margin: 0 32px;

    .m-col {
      display : flex;
      align-items: flex-start;
      flex-direction: column;
      margin-right : 48px;
      justify-content: space-between;
      .col-value {
        font-size : 20px;
        color : #E0ECFF;
      }
    }
  `
const columns = [
  {text : lang['market-price'],value : 50000,prefix : '$'},
  {text : lang['oracle-price'],value : 50020,prefix : '$'},
  {text : lang['dvol'],value : 100,suffix : '%'},
  {text : lang['daily-funding'],value : 100,suffix : '%'},
  {text : lang['market-change'],value : 59322},
  {text : lang['total-net-position'],value : 390.32},
  {text : lang['margin-req'],value : '10%/5%'},
  {text : lang['fee'],value : '1.5% * BTCUSD'},
]
  return(
    <Wrapper>
      {columns.map((col,index) => (
        <div className='m-col' key={index}>
          <UnderlineText text={col.text}/>
          <div className='col-value'>
            {typeof col.value === 'string' ? col.value : <DeriNumberFormat  value = {col.value} prefix={col.prefix} suffix={col.suffix}/>}
          </div>
        </div>
      ))}
  </Wrapper>
  )
}