import { useEffect, useRef, useState } from 'react'
import styled from 'styled-components'

export default function Input({value,unit,step,onChange,styles = {},unitPadding = '24px',inputWidth = '50%',className}){
  const [inputValue,setInputValue] = useState(value)
  const inputRef = useRef(null);
  const change = e => {
    const {value} = e.target
    setInputValue(value)
  }
  const onBlur = (e)=>{
    const {value} = e.target
    setInputValue(value)
    onChange && onChange(value)
  }
  const Wrapper = styled.div`
    display : flex;
    font-size : 14px;
    align-items : center;
    background: rgba(85, 119, 253, 0.1);
    border-radius: 4px;
    justify-content: space-between;
    input {
      background: transparent;
      border-radius: 4px;
      min-width: 240px;
      border: 0;
      width : ${inputWidth};
      height: 56px;
      color : #E0ECFF;
      font-size : 18px;
      padding-left: 16px;
    }
    input:focus {
      outline: none;
    }
    .unit {
      margin-right: ${unitPadding};
    }
  `
  useEffect(()=>{
    if(inputRef.current){
      inputRef.current.focus();
    }
  },[inputValue])
  useEffect(()=>{
    setInputValue(value)
  },[value])
  return (
    <Wrapper className={className} style={{...styles}}>
      <input type='number' value={inputValue} step = {step} ref={inputRef} onBlur={onBlur} onChange={change}/>
      <div className='unit'>{unit}</div>
    </Wrapper>    
  )
}