import React, { useState,useEffect } from 'react'
import RcSlider,{SliderTooltip} from 'rc-slider'
import 'rc-slider/assets/index.css';
export default function Slider({start,min,max,value,className,trackStyle = {},railStyle = { background : 'linear-gradient(270deg, #CE5E5E 9.38%, #CE5E5E 82.22%, #59AE99 89.24%, #59AE99 100%)'},handleStyle = {}}){
  return (
    <RcSlider
        // start={start}
        min={min}
        max={max}
        // value={value}
        className={className}
        defaultValue={value}
        trackStyle={{...trackStyle}}
        handleStyle={{...handleStyle}}
        railStyle={{...railStyle}}
      />
  )
}