import styled from 'styled-components'
import gaugeGraph from '../../../assets/img/gauge-graph.svg'
import DeriNumberFormat from '../../../utils/DeriNumberFormat'
import Button from '../../../components/Button/Button'
import depositIcon from '../../../assets/img/deposit-icon.svg'
import withdrawIcon from '../../../assets/img/withdraw-icon.svg'
import { SECONDARY, DEPOSIT, WITHDRAW } from '../../../utils/Constants'
import UnderlineText from '../../../components/UnderlineText/UnderlineText'
import { show, hide } from 'react-functional-modal'
import MarginPanel from '../../../components/MarginPanel/MarginPanel'
import { observer, inject } from 'mobx-react';

const Wrapper = styled.div`
    border-top: 1px solid #203B60;
    padding : 0 24px 0 48px;
    //账户信息
    .a-info {
      display : flex;
      flex-direction: column;
      margin: 24px auto 32px auto;  
      .content {
        .detail-graph {
          display: flex;
          justify-content: space-between;
          align-items: center;
          .detail {  
            .row.title {
              font-size: 14px;
              color: #E0ECFF;
              margin-top: 0;
            }
            .row {
              font-size: 12px;
              margin-top: 16px;
              .value {
                color : #E0ECFF;
              }
            }
          }
        }
        .info-footer {
          display: flex;
          justify-content: space-between;
          margin-top: 24px;
        }
      }
    }
  }
  `
function AccountInfo({wallet,lang}){
  
  const showMarginPanel = (type) => {
    show(<MarginPanel type={type} lang={lang} wallet={wallet} pool={{}}/>,{
      key  : 'marginPanel',
      fading: true,
      style: {
        background: "rgba(0, 0, 0, 0.4)" ,
        zIndex : 1,
      }
    })
  }

  const closeMarginPanel = () => {
    hide('marginPanel')
  }

  return(
    <Wrapper className='account' >
      <div className='a-info'>
          <div className='content'>
            <div className='detail-graph'>
              <div className='detail'>
                <div className='title row'>{lang['account-info']}</div>
                <div className='row'>
                  <div><UnderlineText text={lang['margin-usage']}/></div>
                  <div className='value'><DeriNumberFormat value='5000' suffix=' BUSD' /></div>
                </div>
                <div className='row'>
                  <div><UnderlineText text={lang['dynamic-effective-balance']}/></div>
                  <div className='value'><DeriNumberFormat value={5030} suffix=' BUSD'/></div>
                </div>
                <div className='row'>
                  <div><UnderlineText text={lang['available-margin']}/></div>
                  <div className='value'><DeriNumberFormat value='51100' suffix=' BUSD'/></div>
                </div>
              </div>
              <div className='graph'>
                <img src ={gaugeGraph} alt=''/>
              </div>
          </div>
          <div className='info-footer'>
            <Button type={SECONDARY} iconsrc={depositIcon} outline label={lang['deposit'].toUpperCase()} onClick={() => showMarginPanel(DEPOSIT)}/> 
            <Button outline type={SECONDARY} iconsrc={withdrawIcon} label={lang['withdraw'].toUpperCase()} onClick={() => showMarginPanel(WITHDRAW)}/>
          </div>
        </div>
      </div>
    </Wrapper>
  )
}
export default inject('wallet')(observer(AccountInfo))