import styled from 'styled-components'
import { inject, observer } from 'mobx-react'

function ContractInfo({lang,trading}){
  const rows = [
    {label : lang['base-token'],key : 'baseToken'},
    {label : lang['symbol'],key : 'symbol'},
    {label : lang['min-trade-volume'],key : 'minTradeVolume'},
    {label : lang['funding-rate-coefficent'],key : 'fundingRateCoefficent'},
    {label : lang['min-initial-margin-ratio'],key : 'minInitialMarginRatio'},
    {label : lang['min-maintenance-margin-ratio'],key : 'minMaintenanceMarginRatio'},
    {label : lang['transaction-fee'],key : 'transactionFee'}
  ]
  const Wrapper = styled.div`
    margin : 8px 0 0 24px;
    .c-title {
      color : #E0ECFF;
      font-size : 14px;
      height: 40px;
    }
    .c-row {
      font-size : 12px;
      height: 24px;
    }
    .c-value {
      color : #E0ECFF；
    }
  `
  return (
    <Wrapper>
      <div className='c-title'>{lang['contract-info']}</div>
      {rows.map((row,index) => (
        <div className='c-row' key={index}>
          <div>{row.label}</div>
          <div className='c-value'></div>
        </div>
      ))}
    </Wrapper>
  )
}
export default inject('trading')(observer(ContractInfo))