import btcIcon from '../../../../assets/img/btc-icon.svg'
import rateDownIcon from '../../../../assets/img/rate-down-icon.svg'
import selectIcon from '../../../../assets/img/arrow-down.svg'
import './symbolInfo.less'
import DeriNumberFormat from '../../../../utils/DeriNumberFormat'
import Version from '../../../../model/Version';

export default function SymbolInfo({pool = {},lang ,zone = Version.zone,rate = 0.02 ,index,onSelect,dataFor }){
  return (
    <div className='symbol-info'>
      <div className='main'>
        <div className='s-left'>
          <div className='zone'>{zone}</div>
          <div className='symbol-name'><img src={btcIcon} alt='btc'/>{pool.selectedSymbol && pool.selectedSymbol.symbol}</div>
        </div>
        <div className='index-area'>
          <div className='index-area-wraper'>
            <div className='rate'><img src={rateDownIcon} alt='' /><DeriNumberFormat value={rate}/></div>
            <div className='index-value'><DeriNumberFormat value={index} decimalScale={4}/></div>
          </div>
          <div className='select-icon'><img data-tip data-for='symbol-panel' data-event='click' src={selectIcon} alt='select' onClick={onSelect}/></div>
        </div>
      </div>
    </div>
  )
}