import { inject, observer } from "mobx-react"
import styled from 'styled-components'

function Position({lang,trading}){
  const colums = [
    {label : lang['symbol'],key : 'symbol'},
    {label : lang['direction'],key : 'direction'},
    {label : lang['position'],key : 'position'},
    {label : lang['entry-price'],key : 'entryPrice'},
    {label : lang['market-price'],key : 'marketPrice'},
    {label : lang['margin-usage'],key : 'marginUsage'},
    {label : lang['liquidity-price'],key : 'liquidityPrice'},
    {label : lang['pnl'],key : 'pnl'},
    {label : lang['acc-funding'],key : 'accFunding'},
    {label : lang['action']}
  ]
  const Wrapper = styled.div`
    margin-top : 16px;
    font-size : 14px;
    overflow: auto;
    white-space: nowrap;
    .p-header {
      display : flex;
      justify-content: space-between;
      height: 40px;
      align-items : center;
    }
    .p-body {
      font-size : 12px;
      height : 128px;
      overflow : auto;
    }
    .p-col {
      width : 95px;
      margin-right : 16px;
    }
  `
  return (
    <Wrapper>
      <div className='p-header'>
        {colums.map((col,index) => (
          <div key={index} className ='p-col'>{col.label}</div>
        ))}
      </div>
      <div className='p-body'>
      </div>
    </Wrapper>
  )
}
export default inject('trading')(observer(Position))